import HeadImgItem from '@/components/content/defimglist/HeadImgItem.vue'
import DefaultList from '@/components/content/defaultlist/DefaultList.vue'

export default {
	components: {
		HeadImgItem,
		DefaultList
	},
	data() {
		return {
			isRemind: true
		}
	},
	methods: {
		//改变消息提醒
		changeMsgRemind() {
			this.isRemind = !this.isRemind
		},
		
		//删除该聊天好友
		delChatFriend() {
			uni.showModal({
				title: '提示',
				content: '确定删除该好友？',
				success: res => {
					console.log(res)
					if(res.confirm) {
						uni.showToast({
							title: '好友已删除',
							icon: 'none',
							success: res => {
								uni.navigateBack()
							}
						})
					}
				},
				fail: err => {
					console.log(err)
				}
			})
		}
	}
}
